import { registerBlockVariation } from '@wordpress/blocks';
import { addFilter } from '@wordpress/hooks';
import icon from './icon.svg';


const BUTTON_BLOCK = 'core/button';
const VARIATION_NAME = 'query-filter-submit-button';
const FORM_ROLE = 'submit';

registerBlockVariation(
  BUTTON_BLOCK,
  {
    name: VARIATION_NAME,
    title: 'Query Filter Submit Button',
    icon,
    attributes: {
      queryFilterForm_role: FORM_ROLE,
      amplifyVariation: VARIATION_NAME,
      text: 'Submit',
    },
    isActive: ['amplifyVariation', 'queryFilterForm_role'],
  }
);

addFilter(
  'blocks.registerBlockType',
  `${BUTTON_BLOCK}/${VARIATION_NAME}`,
  (settings, name) => {
    if (name !== BUTTON_BLOCK) {
      return settings;
    }

    return {
      ...settings,
      attributes: {
        ...settings.attributes,
        amplifyVariation: {
          type: 'string',
          default: null,
        },
        queryFilterForm_role: {
          type: 'string',
          default: '',
        }
      }
    };
  }
);
