import {
  InnerBlocks,
  useBlockProps,
  useInnerBlocksProps,
} from '@wordpress/block-editor';
import tabButtonsDef from '../tab-buttons/block.json';
import tabButtonDef from '../tab-button/block.json';
import tabContentsDef from '../tab-contents/block.json';
import tabContentPaneDef from '../tab-content-pane/block.json';
import blockDef from './block.json';


const createTabButton = (buttonNumber) => ([
  tabButtonDef.name,
  {},
  [
    ['core/paragraph', { content: `Tab ${buttonNumber}` }],
  ]
]);

const createTabContentPanes = (tabNumber) => ([
  tabContentPaneDef.name,
  {},
  [
    ['core/paragraph', { content: `Content pane ${tabNumber}` }],
  ]
]);

// Start off with a set of three tabs and content panes.
const editTemplate = [
  [
    tabButtonsDef.name, 
    {},
    [
      createTabButton(1),
      createTabButton(2),
      createTabButton(3),
    ]
  ],
  [
    tabContentsDef.name, 
    {},
    [
      createTabContentPanes(1),
      createTabContentPanes(2),
      createTabContentPanes(3),
    ]
  ],
];

export function Edit() {
  return (
    <div {...useBlockProps({className: `wp-block-mbm-tab-layout`})}>
      <InnerBlocks template={editTemplate} templateLock={false} />
    </div>
  );
}

export function Save() {
  const context = {
    selectedIndex: 0,
  };
  
  return (
    <div
      {...useInnerBlocksProps.save(useBlockProps.save())}
			data-wp-interactive={`{ "namespace": "${blockDef.amplify.viewNamespace}" }`}
			data-wp-init="callbacks.init"
			data-wp-context={JSON.stringify(context)}
    />
  );
}
