import {
  PanelBody,
  ToggleControl,
} from '@wordpress/components';


const CaptionControls = ({
  attributes,
  setAttributes,
}) => {
  const {
    isDynamicCaptionVisible,
  } = attributes;

  return (
    <PanelBody title="Caption Controls">
      <ToggleControl
        label="Include image caption"
        help={
          isDynamicCaptionVisible
            ? 'Caption will be included after image.'
            : `Caption will not be shown.`
        }
        checked={isDynamicCaptionVisible}
        onChange={(value) => setAttributes({ isDynamicCaptionVisible: value })}
      />
    </PanelBody>
  );
};

export default CaptionControls;
