import {
  InnerBlocks,
  useBlockProps,
  useInnerBlocksProps,
} from '@wordpress/block-editor';
import blockDef from './block.json';
import { useTabLayoutContext } from '../editor-support';


const editTemplate = [
  ['core/paragraph', { content: 'Tab Button' }],
];

export function Edit(props) {
  useTabLayoutContext(props.clientId, blockDef.name);

  // console.log(`[TabButton] ${props.clientId} is selected:`, isTabSelected)

  return (
    <div {...useBlockProps()}>
      <InnerBlocks template={editTemplate} templateLock={false} />
    </div>
  );
}

export function Save() {
  const context = {
    index: null,
  };

  return (
    <div
      {...useInnerBlocksProps.save(useBlockProps.save())}
      data-wp-interactive={`{ "namespace": "${blockDef.amplify.viewNamespace}" }`}
      data-wp-init="callbacks.init"
      data-wp-on--click="actions.onClick"
      data-wp-context={JSON.stringify(context)}
      data-wp-class--wp-block-mbm-tab-button--selected="state.isSelected"
    />
  );
}
