import { __ } from '@wordpress/i18n';
import {
	useBlockProps,
	useInnerBlocksProps,
	InspectorControls,
} from '@wordpress/block-editor';
import {
	__experimentalNumberControl as NumberControl,
	PanelBody,
	PanelRow,
	SelectControl,
	ToggleControl,
} from '@wordpress/components';
import { usePostTaxonomies } from './use-post-taxonomies';


function EditControls({
	taxonomySlug,
	taxonomies,
	minTermsCount,
	maxTermsCount,
	setAttributes
}) {
	return (
		<InspectorControls>
			<PanelBody
				title={__('Terms')}
				opened={true}
			>
				<SelectControl
					label="Taxonomy"
					value={taxonomySlug}
					options={taxonomies.map((taxonomy) => ({
						label: taxonomy.name,
						value: taxonomy.slug,
					}))}
					onChange={(taxonomySlug) => setAttributes({ taxonomySlug })}
				/>

				{/* Show a toggle control that changes the minTermsCount to -1 if enabled, 0 if not. If minTermsCount does not equal -1, show a number control for minTermsCount. */}
				<ToggleControl
					label="Require a minimum number of terms"
					checked={minTermsCount !== -1}
					onChange={(hasMinimum) => setAttributes({
						minTermsCount: hasMinimum ? 0 : -1,
					})}
				/>
				{minTermsCount !== -1 && (
					<NumberControl
						label="Minimum number of terms"
						value={minTermsCount}
						shiftStep={1}
						min={0}
						onChange={(value) => setAttributes({
							minTermsCount: parseInt(value, 10),
						})}
					/>
				)}

				{/* Show a toggle control that changes the maxTermsCount to -1 if enabled, 0 if not. If maxTermsCount does not equal -1, show a number control for maxTermsCount. */}
				<ToggleControl
					label="Require a maximum number of terms"
					checked={maxTermsCount !== -1}
					onChange={(hasMaximum) => setAttributes({
						maxTermsCount: hasMaximum ? 0 : -1,
					})}
				/>
				{maxTermsCount !== -1 && (
					<NumberControl
						label="Maximum number of terms"
						value={maxTermsCount}
						shiftStep={1}
						min={0}
						onChange={(value) => setAttributes({
							maxTermsCount: parseInt(value, 10),
						})}
					/>
				)}
			</PanelBody>
		</InspectorControls>
	);
}

function Edit({
	context,
	attributes,
	setAttributes,
}) {
	const {
		tagName,
		taxonomySlug,
		minTermsCount,
		maxTermsCount,
	} = attributes;
	const TagName = tagName;

	const postTaxonomies = usePostTaxonomies(context.postType);

	return (
		<>
			<EditControls
				setAttributes={setAttributes}
				taxonomySlug={taxonomySlug}
				taxonomies={postTaxonomies || []}
				minTermsCount={minTermsCount}
				maxTermsCount={maxTermsCount}
			/>

			<TagName {...useInnerBlocksProps(useBlockProps())} />
		</>
	);
};

export default Edit;
