import {
  InnerBlocks,
  useBlockProps,
  useInnerBlocksProps,
} from '@wordpress/block-editor';
import tabContentPaneDef from '../tab-content-pane/block.json';


export function Edit() {
  return (
    <div {...useBlockProps()}>
      <InnerBlocks 
        template={[]} 
        templateLock={false} 
        allowedBlocks={[tabContentPaneDef.name]}
      />
    </div>
  );
}

export function Save() {
  return (
    <div
      {...useInnerBlocksProps.save(useBlockProps.save())}
    />
  );
}
