import { registerBlockType } from '@wordpress/blocks';
import blockDef from './block.json';
import icon from './icon.svg';
import { 
	edit, 
	defaultTagType,
} from './edit';
import save from './save';
import {
  ariaLabelAttribute,
} from '../shared/accessibility/AriaLabelPanel';

registerBlockType(
	blockDef, 
	{
		icon,
		edit,
		save,
		attributes: {
			tagType: {
				type: 'string',
				default: defaultTagType,
			},
			...ariaLabelAttribute,
		},
	}
);
