import { 
  PanelBody, 
  PanelRow, 
  ToggleControl,
  SelectControl,
} from '@wordpress/components';
import { addFilter } from '@wordpress/hooks';
import { InspectorControls } from '@wordpress/block-editor';


const QUERY_BLOCK_SLUG = 'core/query';
const DEFAULT_POST_EXCLUSION_METHOD = 'cache-friendly';

const PostExclusionControls = ({
  attributes,
  setAttributes
}) => {
  const { 
    isCurrPostExcluded = false,
    postExclusionMethod = DEFAULT_POST_EXCLUSION_METHOD,
  } = attributes;

  return (
    <PanelBody title="Excluded Posts">
      <PanelRow>
        <ToggleControl
          label="Exclude Current Post?"
          help={
            isCurrPostExcluded
              ? 'Current post is excluded from query results.'
              : 'Current post could be included in query results.'
          }
          checked={isCurrPostExcluded}
          onChange={(newValue) => {
            setAttributes({
              isCurrPostExcluded: newValue
            });
          }}
        />
      </PanelRow>
      <PanelRow>
        {isCurrPostExcluded && (
          <SelectControl
            label="Post Exclusion Method"
            value={postExclusionMethod}
            options={[
              { label: 'Cache-Friendly', value: 'cache-friendly' },
              { label: 'Pagination-Friendly', value: 'pagination-friendly' },
            ]}
            onChange={(value) => setAttributes({ postExclusionMethod: value })}
            help={
              postExclusionMethod === 'cache-friendly'
                ? 'The query will run quickly and be cache-friendly, but the post exclusion algorithm will interfere with pagination results, and could result in duplicates.'
                : 'The query will be significantly slower and site performance will degrade, but the post exclusion algorithm will not interfere with pagination results.'
            }
          />
        )}
      </PanelRow>
    </PanelBody>
  );
};

const withExtraControls = (BlockEdit) => (props) => {
  const {name} = props;

  if (name !== QUERY_BLOCK_SLUG) {
    return <BlockEdit {...props} />;
  }

  let conditionalControls = (
    <InspectorControls>
      <PostExclusionControls {...props} />
    </InspectorControls>
  );
  
  // Hide controls if we detect a variation. Assume those variations
  // override the queried posts and make the exclude post control unnecessary.
  // if (typeof props.attributes?.namespace === 'string') {
  //   conditionalControls = null;
  // }
  
  return (
    <>
      <BlockEdit {...props} />
      {conditionalControls}
    </>
  );
};

addFilter('editor.BlockEdit', QUERY_BLOCK_SLUG, withExtraControls);

addFilter(
  'blocks.registerBlockType',
  'mbm/query-loop-exclude-post-query',
  (settings, name) => {
    if (name !== QUERY_BLOCK_SLUG) {
      return settings;
    }

    return {
      ...settings,
      attributes:
      {
        ...settings.attributes,
        isCurrPostExcluded: {
          type: 'boolean',
          default: false
        },
        postExclusionMethod: {
          type: 'string',
          default: DEFAULT_POST_EXCLUSION_METHOD
        },
      }
    };
  }
);
