import { registerBlockVariation } from '@wordpress/blocks';
import { addFilter } from '@wordpress/hooks';
import icon from './icon.svg';


const ROOT_BLOCK = 'core/buttons';
const VARIATION_NAME = 'query-filter-buttons';

registerBlockVariation(
  ROOT_BLOCK,
  {
    name: VARIATION_NAME,
    title: 'Query Filter Buttons',
    icon,
    attributes: {
      amplifyVariation: VARIATION_NAME,
    },
    isActive: ['amplifyVariation'],
    innerBlocks: [
      [
        "core/button",
        {
          queryFilterForm_role: 'submit',
          amplifyVariation: 'query-filter-submit-button',
          text: 'Submit',
        }
      ]
    ],
  }
);

addFilter(
  'blocks.registerBlockType',
  `${ROOT_BLOCK}/${VARIATION_NAME}`,
  (settings, name) => {
    if (name !== ROOT_BLOCK) {
      return settings;
    }

    return {
      ...settings,
      attributes: {
        ...settings.attributes,
        amplifyVariation: {
          type: 'string',
          default: null,
        },
      }
    };
  }
);
