import {
  InnerBlocks,
  useBlockProps,
  useInnerBlocksProps,
} from '@wordpress/block-editor';
import { registerBlockType } from '@wordpress/blocks';

import blockDef from './block.json';
import icon from './icon.svg';


const titleBlockClass = `wp-block-mbm-innerblock-accordion-title`;
const iconClass = `${titleBlockClass}__icon`;
const iconBlockSlug = `outermost/icon-block`;

const editTemplate = [
  // Make H4 by default
  ['core/heading', { level: 4, placeholder: 'Accordion Title' }],
  [
    iconBlockSlug,
    {
      iconName: 'wordpress-plusCircle',
      className: `${iconClass}--open`,
      metadata: {
        name: "Open Icon"
      },
    }
  ],
  [
    iconBlockSlug,
    {
      iconName: 'wordpress-cancelCircleFilled',
      className: `${iconClass}--closed`,
      metadata: {
        name: "Close Icon"
      },
    }
  ],
];

function Edit() {
  return (
    <div {...useBlockProps()}>
      <InnerBlocks template={editTemplate} templateLock="all" />
    </div>
  );
}

function Save() {
  return (
    <summary
      {...useInnerBlocksProps.save(useBlockProps.save())}
    />
  );
}

registerBlockType(
  blockDef,
  {
    icon: icon,
    edit: Edit,
    save: Save,
  }
);
