import { registerBlockVariation } from '@wordpress/blocks';
import { addFilter } from '@wordpress/hooks';


const BUTTON_BLOCK = 'core/button';
const VARIATION_NAME = 'query-filter-reset-button';
const FORM_ROLE = 'reset';

registerBlockVariation(
  BUTTON_BLOCK,
  {
    name: VARIATION_NAME,
    title: 'Query Filter Reset Button',
    attributes: {
      queryFilterForm_role: FORM_ROLE,
      text: 'Reset',
    },
    isActive: (blockAttributes, variationAttributes) => {
      return blockAttributes.queryFilterForm_role === FORM_ROLE;
    },
  }
);

addFilter(
  'blocks.registerBlockType',
  `${BUTTON_BLOCK}/${VARIATION_NAME}`,
  (settings, name) => {
    if (name !== BUTTON_BLOCK) {
      return settings;
    }

    return {
      ...settings,
      attributes: {
        ...settings.attributes,
        queryFilterForm_role: {
          type: 'string',
          default: '',
        }
      }
    };
  }
);
