import classNames from 'classnames';
import { __ } from '@wordpress/i18n';
import {
	InnerBlocks,
	InspectorControls,
	useBlockProps,
	useInnerBlocksProps,
} from '@wordpress/block-editor';
import {
	PanelBody,
	PanelRow,
	SelectControl,
} from '@wordpress/components';
import {
	ariaLabelParser,
	AriaLabelPanel,
} from '../shared/accessibility/AriaLabelPanel';


const tagTypeOptions = [
	{
		label: 'Article - <article>',
		value: 'article',
	},
	{
		label: 'Aside - <aside>',
		value: 'aside',
	},
	{
		label: 'Div - <div>',
		value: 'div',
	},
	{
		label: 'Figure - <figure>',
		value: 'figure',
	},
	{
		label: 'Footer - <footer>',
		value: 'footer',
	},
	{
		label: 'Header - <header>',
		value: 'header',
	},
	{
		label: 'List Item - <li>',
		value: 'li',
	},
	{
		label: 'Nav - <nav>',
		value: 'nav',
	},
	{
		label: 'Ordered List - <ol>',
		value: 'ol',
	},
	{
		label: 'Section - <section>',
		value: 'section',
	},
	{
		label: 'Unordered List - <ul>',
		value: 'ul',
	},
];
export const defaultTagType = 'div';

const Block = (props) => {
	const {
		tagType: Tag,
	} = props.attributes;

	const blockProps = useInnerBlocksProps(useBlockProps());

	return (
		<Tag
			{...blockProps}
			{...ariaLabelParser(props)}
		/>
	);
};

const Controls = (props) => {
	const { tagType } = props.attributes;

	return (
		<InspectorControls>
			<PanelBody
				title="Settings"
			>
				<PanelRow>
					<SelectControl
						label='Tag Type'
						value={tagType}
						options={tagTypeOptions}
						onChange={(tagType) => props.setAttributes({ tagType })}
					/>
				</PanelRow>
			</PanelBody>
			<AriaLabelPanel {...props} />
		</InspectorControls>
	);
};

export const edit = ({ attributes, setAttributes }) => {
	return (
		<>
			<Controls
				attributes={attributes}
				setAttributes={setAttributes}
			/>
			<Block
				attributes={attributes}
			/>
		</>
	);
};

export default edit;
