import {
  InnerBlocks,
  useBlockProps,
  useInnerBlocksProps,
} from '@wordpress/block-editor';
import { registerBlockType } from '@wordpress/blocks';

import blockDef from './block.json';
import icon from './icon.svg';


const editTemplate = [
  ['core/paragraph', { placeholder: 'Accordion body content...' }],
];

function Edit() {
  return (
    <div {...useBlockProps()}>
      <InnerBlocks template={editTemplate} templateLock={false} />
    </div>
  );
}

function Save() {
  return (
    <div
      {...useInnerBlocksProps.save(useBlockProps.save())}
    />
  );
}

registerBlockType(
  blockDef,
  {
    icon: icon,
    edit: Edit,
    save: Save,
  }
);
