import { useEffect } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { store as tabLayoutStore } from './editor-data';
import tabLayoutBlockDef from './tab-layout/block.json';

/*
  Two key states here:
    * Whether a block is visible
    * Whether a block is selected
*/

// WARN: might need to split this up.
export const useTabLayoutContext = (blockClientId, blockName) => {
  const { setSelectedTabIndex } = useDispatch(tabLayoutStore);

  const tabLayoutContext = useSelect((select) => {
    const {
      getBlockIndex,
      getSelectedBlock,
      getBlockName,
      getBlockParents,
      getBlock,
    } = select(blockEditorStore);

    const {
      getSelectedTabIndex,
    } = select(tabLayoutStore);

    let selectedTabBlock = null;

    let selectedBlock = getSelectedBlock();
    const selectedBlockName = getBlockName(selectedBlock?.clientId);
    
    // Get tab layout parent of provided block.
    const blockParentIDs = getBlockParents(blockClientId);
    // getBlockParents returns the client IDs of a block's parents in order of
    // root block to closest parent. Revese the array so we're looking at the
    // nearest ancestors first.
    blockParentIDs.reverse();
    const tabLayoutClientId = blockParentIDs.find((clientId) => {
      return getBlockName(clientId) === tabLayoutBlockDef.name;
    });


    if (selectedBlockName === blockName) {
      selectedTabBlock = selectedBlock;
    } else if (selectedBlock !== null) {
      const selectedBlockParentIDs = getBlockParents(selectedBlock?.clientId);
      // Reverse to search nearest ancestors first.
      selectedBlockParentIDs.reverse();
      const tabParentId = selectedBlockParentIDs.find((clientId) => {
        return getBlockName(clientId) === blockName;
      });

      if (tabParentId !== undefined) {
        selectedTabBlock = getBlock(tabParentId);
      }
    }

    return {
      isTabSelected: selectedTabBlock !== null,
      selectedTabBlock,
      blockIndex: getBlockIndex(blockClientId),
      selectedTabIndex: getSelectedTabIndex(tabLayoutClientId),
      tabLayoutClientId,
    };
  });

  const {
    isTabSelected,
    selectedTabBlock,
    blockIndex,
    tabLayoutClientId,
  } = tabLayoutContext;

  useEffect(() => {
    if (selectedTabBlock?.clientId === blockClientId) {
      console.log(`[${blockName}] ${blockClientId} is selected. Index: ${blockIndex}.`)

      setSelectedTabIndex(tabLayoutClientId, blockIndex);

      console.log(`[${blockName}] ${blockClientId} updated selectedTabIndex to ${blockIndex}.`)
    }
  }, [isTabSelected, selectedTabBlock?.clientId, blockIndex]);

  return tabLayoutContext;
};
