import { __ } from '@wordpress/i18n';
import {
  useBlockProps,
  useInnerBlocksProps,
} from '@wordpress/block-editor';
import tabButtonBlockDef from '../tab-button/block.json';


export function Edit() {
  const blockProps = useBlockProps();
	const combinedBlockProps = useInnerBlocksProps(
		blockProps,
		{
      template: [],
      templateLock: false,
      allowedBlocks: [tabButtonBlockDef.name],
		}
	);

  return (
    <>
      <div {...combinedBlockProps} />
    </>
  );
}

export function Save() {
  return (
    <div
      {...useInnerBlocksProps.save(useBlockProps.save())}
    />
  );
}
