import {
  useBlockProps,
  useInnerBlocksProps,
} from '@wordpress/block-editor';
import blockDef from './block.json';
import { useTabLayoutContext } from '../editor-support';


export function Edit(props) {
  const {
    blockIndex,
    selectedTabIndex,
  }  = useTabLayoutContext(props.clientId, blockDef.name);
  const isContentPaneTabSelected = blockIndex === selectedTabIndex;
  console.log(`[TabContentPane] selectedTabIndex: ${selectedTabIndex}. Content pane index: ${blockIndex}. Is selected: ${isContentPaneTabSelected}`)

  const blockProps = useInnerBlocksProps(
    useBlockProps({
      style: {
        display: isContentPaneTabSelected === true ? 'unset' : 'none',
      }
    })
  );

  return (
    <div {...blockProps} />
  );
}

export function Save() {
  const context = {
    index: null,
  };

  return (
    <div
      {...useInnerBlocksProps.save(useBlockProps.save())}
			data-wp-interactive={`{ "namespace": "${blockDef.amplify.viewNamespace}" }`}
			data-wp-init="callbacks.init"
			data-wp-context={JSON.stringify(context)}
      data-wp-class--wp-block-mbm-tab-content-pane--selected="state.isSelected"
    />
  );
}
