import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import {
	InnerBlocks,
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';
import {
	PanelBody,
	PanelRow,
	SelectControl,
	Spinner
} from '@wordpress/components';
import { useSelect, useDispatch } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useEffect } from '@wordpress/element';
import {
	useQueryAttributes,
	setQueryAttributesTaxonomyTag,
	useIsBlockPresent,
} from './editor-support';
import blockDef from './block.json';
import icon from './icon.svg';
import './editor.scss';


const blockClass = `wp-block-mbm-query-taxonomy-filter`;
const iconClass = `${blockClass}__icon`;
const editTemplate = [
	[
		'outermost/icon-block',
		{
			iconName: 'wordpress-plusCircle',
			className: `${iconClass}--open`,
			metadata: {
				name: "Open Icon"
			},
		}
	],
	[
		'outermost/icon-block',
		{
			iconName: 'wordpress-cancelCircleFilled',
			className: `${iconClass}--closed`,
			metadata: {
				name: "Close Icon"
			},
		}
	],
];

const findTaxonomy = (taxonomies, slug) => taxonomies.find((tax) => tax.slug === slug);

const EditLoadingSpinner = () => (
	<div {...useBlockProps()}>
		<Spinner />
	</div>
);

function EditComponent({
	attributes,
	setAttributes,
	context,
	clientId,
}) {
	const {
		taxonomySlug = null,
	} = attributes;
	// console.log(`[mbm-query-taxonomy-filter] context.query:`, context?.query);

	// Retrieve the post type for the query from the ancestor Query Loop's 
	// provided context.
	const queryAttributes = context?.query || {};
	const queryPostType = queryAttributes?.postType;
	const queryBlock = useQueryAttributes(clientId);
	const { updateBlockAttributes } = useDispatch(blockEditorStore);
	let termsOperator = 'IN';
	if (taxonomySlug != null) {
		const blockFilterConfig = queryBlock.queryAttributes.query.ampTaxonomies?.[taxonomySlug];
		if (blockFilterConfig != null) {
			termsOperator = blockFilterConfig.termsOperator || 'IN';
		}
	}

	const taxonomies = useSelect((select) => {
		return select('core').getTaxonomies({
			// Only fetch taxonomies registered for the post type of the current 
			// query loop.
			type: queryPostType,
			// Ensure all taxonomies are fetched (no results limit).
			per_page: -1,
			context: 'view',
		});
	}, [queryPostType]);

	let selectedTaxonomy = findTaxonomy(taxonomies || [], taxonomySlug);

	const updateQueryBlockConfig = (newTaxonomySlug, updatedConfig) => {
		if (newTaxonomySlug == null) {
			return;
		}

		const query = { ...queryAttributes };
		const ampTaxonomies = { ...(query.ampTaxonomies || {}) };

		// console.log(`[mbm-query-taxonomy-filter] original ampTaxonomies:`, ampTaxonomies, JSON.stringify(ampTaxonomies, null, 2));
		
		let taxFilterConfig = ampTaxonomies[taxonomySlug] || {};
		
		ampTaxonomies[newTaxonomySlug] = {
			...taxFilterConfig,
			...updatedConfig,
		};
		query.ampTaxonomies = ampTaxonomies;
		// console.log(`[mbm-query-taxonomy-filter] updated ampTaxonomies:`, ampTaxonomies, JSON.stringify(ampTaxonomies, null, 2));

		updateBlockAttributes(
			queryBlock.parentQueryBlockId,
			{
				query,
			}
		);
	}

	useEffect(
		() => {
			if (selectedTaxonomy == null) {
				if (Array.isArray(taxonomies)) {
					let taxonomySlug = null;

					if (taxonomies.length > 0) {
						taxonomySlug = taxonomies[0].slug;
					}

					setAttributes({ taxonomySlug });
					updateQueryBlockConfig(taxonomySlug, { termsOperator: 'IN' })
				}
			}
		},
		[taxonomies, selectedTaxonomy]
	);

	let taxonomyOptions = taxonomies != null
		? taxonomies.map((taxonomy) => ({ label: taxonomy.name, value: taxonomy.slug, key: taxonomy.slug }))
		: [];

	return (<>
		<div {...useBlockProps()}>
			{!Array.isArray(taxonomies) && (
				<Spinner />
			)}
			{Array.isArray(taxonomies) && selectedTaxonomy != null ? (
				<p>{selectedTaxonomy.name} terms filter.</p>
			) : (
				<p>No taxonomy is configured for this filter.</p>
			)}
			<InnerBlocks template={editTemplate} templateLock="all" />
		</div>
		<InspectorControls>
			<PanelBody title="Taxonomy">
				<PanelRow>
					{(taxonomyOptions.length > 0 && taxonomySlug != null) ? (
						<SelectControl
							label="Taxonomy"
							value={taxonomySlug}
							options={taxonomyOptions}
							onChange={(taxonomySlug) => {
								// Only altering the taxonomy slug here.
								updateQueryBlockConfig(taxonomySlug, {});
								setAttributes({ taxonomySlug });
							}}
						/>
					) : (
						<p>No taxonomies could be found for post type '{queryPostType}'.</p>
					)}
				</PanelRow>
				<PanelRow>
					<SelectControl
						label="Terms Relation"
						value={termsOperator}
						options={[
							{ label: 'Or', value: 'IN' },
							{ label: 'And', value: 'AND' },
						]}
						onChange={(termsOperator) => updateQueryBlockConfig(taxonomySlug, { termsOperator })}
						help={termsOperator === 'AND'
							? `Only show posts that match all of the terms selected by guests.`
							: `Show posts that match any of the terms selected by guests.`
						}
					/>
				</PanelRow>
			</PanelBody>
		</InspectorControls>
	</>);
};

registerBlockType(
	blockDef,
	{
		icon,
		edit: EditComponent,
		// This is a dynamic block, but we still need to save the innerblocks 
		// (the selected open/close icons).
		save: () => (<InnerBlocks.Content />)
	}
);
