import { registerBlockVariation } from '@wordpress/blocks';
import { addFilter } from '@wordpress/hooks';
import { InspectorControls } from '@wordpress/block-editor';
import TaxonomyControls from './TaxonomyControls';


const QUERY_BLOCK_SLUG = 'core/query';
const VARIATION_NAME = 'amplify/related-posts-query';

const isVariation = (props) => {
  const {
    attributes: { namespace }
  } = props;

  return namespace && namespace === VARIATION_NAME;
};


const withExtraControls = (BlockEdit) => (props) => {
  if (!isVariation(props)) return (<BlockEdit {...props} />);

  // console.log(`[withExtraControls] attributes:`, props.attributes)
  // console.log(`[withExtraControls] context:`, props.context)

  return (
    <>
      <BlockEdit {...props} />
      { props.isSelected && (
        <InspectorControls>
          <TaxonomyControls {...props} />
        </InspectorControls>
      )}
    </>
  );
};

addFilter('editor.BlockEdit', QUERY_BLOCK_SLUG, withExtraControls);

registerBlockVariation(
  QUERY_BLOCK_SLUG,
  {
    name: VARIATION_NAME,
    title: 'Related Post Query',
    description: `Retrieve a select list of posts related to the current post. Currently, this block is meant to be used on the single post template and no where else.`,
    isActive: ['namespace'],

    attributes: {
      namespace: VARIATION_NAME,
      query: {
        postType: 'post',
        inherit: false,
        orderby: 'date',
      },
      // Our own default taxonomy configuration.
      taxonomySlug: 'category',
      includeChildren: true,
      includeParents: true,
    },

    // allowedControls: [],

    innerBlocks: [
      [
        'core/post-template',
        {},
        [
          ['core/post-title'],
        ],
      ]
    ]
  }
);
