import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import {
	useBlockProps,
} from '@wordpress/block-editor';

import blockDef from './block.json';
import icon from './icon.svg';


function Component({ blockProps }) {
	return (
		<>
			<div
				{...blockProps}
			>
				<p>Showing X â€“ X of X posts</p>
			</div>
		</>
	);
}

function EditComponent({ }) {
	return <Component blockProps={useBlockProps()} />
};

// Warning: do not copy this implementation, it is incorrect. This is a 
// server-side rendered block, and therefore this save implementation is 
// unnecessary.
function SaveComponent({ }) {
	return <Component blockProps={useBlockProps.save()} />
}

registerBlockType(
	blockDef,
	{
		icon,
		edit: EditComponent,
		// Warning: this save component isn't necessary, but removing it will
		// cause block validation failures.
		save: SaveComponent,
	}
);
