import { registerBlockVariation } from '@wordpress/blocks';
import { addFilter } from '@wordpress/hooks';
import { InspectorControls } from '@wordpress/block-editor';
import PostSelectionControls from './PostSelectionControls';


const QUERY_BLOCK_SLUG = 'core/query';
const VARIATION_NAME = 'mbm/selected-post-query';

const isVariation = (props) => {
  const {
    attributes: { namespace }
  } = props;

  return namespace && namespace === VARIATION_NAME;
};


const withExtraControls = (BlockEdit) => (props) => {
  return isVariation(props) ? (
    <>
      <BlockEdit {...props} />
      <InspectorControls>
        <PostSelectionControls {...props} />
      </InspectorControls>
    </>
  ) : (
    <BlockEdit {...props} />
  );
};

addFilter('editor.BlockEdit', QUERY_BLOCK_SLUG, withExtraControls);

registerBlockVariation(
  QUERY_BLOCK_SLUG,
  {
    name: VARIATION_NAME,
    title: 'Selected Post Query',
    description: `Retrieve a select list of posts.`,
    isActive: ['namespace'],

    attributes: {
      namespace: VARIATION_NAME,
      query: {
        // perPage: 1,
        postType: 'post',
        inherit: false,
        include: [],
        // This must be `date`, not `include`, or the initial rendering fails.
        orderby: 'date',
        randomizeResults: false,
      },
    },

    // allowedControls: [],

    innerBlocks: [
      [
        'core/post-template',
        {},
        [
          ['core/post-title'],
        ],
      ]
    ]
  }
);
