import { addFilter } from '@wordpress/hooks';
import { InspectorControls } from '@wordpress/block-editor';
import CaptionControls from './CaptionControls';
import { RichText } from '@wordpress/block-editor';

const BLOCK_SLUG = 'core/post-featured-image';

const isVariation = (props) => {
  return props.name === BLOCK_SLUG;
};

const withExtraControls = (BlockEdit) => (props) => {
  // Ensure that the block is the one we're targeting.
  if (!isVariation(props)) return (<BlockEdit {...props} />);

  return (
    <>
      <BlockEdit {...props} />
      {/* WARN: Failed attempts to include a sample caption in the editor view. */}
      {/* <figcaption>Here would go the image caption.</figcaption> */}
      {/* <RichText
        tagName="p"
        value={'Here would go the image caption.'}
        onChange={(value) => setAttributes({ richTextContent: value })}
        placeholder="Enter your text here..."
      /> */}
      <InspectorControls>
        <CaptionControls {...props} />
      </InspectorControls>
    </>
  );
};

addFilter('editor.BlockEdit', BLOCK_SLUG, withExtraControls);

// Add attributes to the block.
addFilter(
  'blocks.registerBlockType',
  BLOCK_SLUG + '/dynamic-caption-attributes',
  (settings, name) => {
    if (name !== BLOCK_SLUG) {
      return settings;
    }
    
    return {
      ...settings,
      attributes: {
        ...settings.attributes,
        isDynamicCaptionVisible: {
          type: 'boolean',
          default: false,
        },
      },
    };
  }
);
