import { useEffect } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';

const coreQueryBlockName = 'core/query';

export const useQueryAttributes = (blockClientId) => {
  const queryBlock = useSelect((select) => {
    const {
      getBlockAttributes,
      getBlockName,
      getBlockParents,
      getBlock,
    } = select(blockEditorStore);

    // Get tab layout parent of provided block.
    const blockParentIDs = getBlockParents(blockClientId);
    // getBlockParents returns the client IDs of a block's parents in order of
    // root block to closest parent. Revese the array so we're looking at the
    // nearest ancestors first.
    blockParentIDs.reverse();
    const parentQueryBlockId = blockParentIDs.find((clientId) => {
      return getBlockName(clientId) === coreQueryBlockName;
    });

    const queryAttributes = getBlockAttributes(parentQueryBlockId);

    return {
      parentQueryBlockId,
      queryAttributes,
    };
  });

  return queryBlock;
};

