// WARN: this doesn't really work.
export const useIsBlockPresent = (blockClientId) => {
  const isBlockPresent = useSelect((select) => {
    const {
      getBlockParents,
      getBlock,
    } = select(blockEditorStore);

    const block = getBlock(blockClientId);
    if (!block) {
      return false;
    }

    // Get tab layout parent of provided block.
    const blockParentIDs = getBlockParents(blockClientId);
    // getBlockParents returns the client IDs of a block's parents in order of
    // root block to closest parent. Revese the array so we're looking at the
    // nearest ancestors first.

    // Get the last element in the parent IDs array, first checking to make sure it's not empty.
    if (blockParentIDs.length === 0) {
      return false;
    }
    const parentBlockId = blockParentIDs[blockParentIDs.length - 1];

    const parentBlock = getBlock(parentBlockId);

    if (!parentBlock) {
      return false;
    }

    if (parentBlock && parentBlock.innerBlocks && parentBlock.innerBlocks.includes(block)) {
      return true;
    }

    // If the parent does not have this block as a child, the block is gone
    return false;
  });

  return isBlockPresent;
}

export const setQueryAttributesTaxonomyTag = (blockClientId, taxonomyTag) => {
  const queryBlock = useQueryAttributes(blockClientId);

  const query = { ...queryBlock.queryAttributes.query };
  query.ampTaxonomies = [taxonomyTag];
  console.log(`[mbm-query-taxonomy-filter] updated query attrs:`, query);
  useDispatch(blockEditorStore).updateBlockAttributes(
    queryBlock.parentQueryBlockId,
    {
      query,
    }
  );
};
