import { __ } from '@wordpress/i18n';
import {
	useBlockProps,
	useInnerBlocksProps,
} from '@wordpress/block-editor';
import {
	ariaLabelParser,
} from '../shared/accessibility/AriaLabelPanel';

export const save = (props) => {
	const {
		tagType: Tag,
	} = props.attributes;

	return (
		<Tag
			{...useInnerBlocksProps.save(useBlockProps.save())}
			{...ariaLabelParser(props)}
		/>
	);
};

export default save;
