import { 
  PanelBody, 
  SelectControl,
  TextControl,
} from '@wordpress/components';
import { registerBlockVariation } from '@wordpress/blocks';
import { addFilter } from '@wordpress/hooks';
import { InspectorControls } from '@wordpress/block-editor';
import { useSelect } from '@wordpress/data';
import { useState } from '@wordpress/element';


const QUERY_BLOCK_SLUG = 'core/query';
const VARIATION_NAME = 'mbm/single-post-query';

const isVariation = (props) => {
  const {
    attributes: { namespace }
  } = props;

  return namespace && namespace === VARIATION_NAME;
};

const PostSelector = ({
  selected = null,
  onSelect = () => undefined,
  postTypeSlug,
}) => {
  const [searchTerm, setSearchTerm] = useState('');
  
  const postTypes = useSelect((select) => {
		return select('core')
      .getPostTypes()
	});
  const postType = postTypes 
    ? postTypes.find((postType) => postType.slug === postTypeSlug)
    : null;
  const postTypeSingularName = postType?.labels?.singular_name || 'Post';
  const postTypePluralName = postType?.name || 'Posts';

  const { pages } = useSelect((select) => {
    const { getEntityRecords } = select('core');

    // Post query args
    const query = {
      status: 'publish',
      per_page: 50,
      search: searchTerm,
    };

    return {
      pages: getEntityRecords('postType', postTypeSlug, query),
    }
  });

  const noSelectionValue = 0;
  const options = [];
  if (pages) {
    options.push({ value: noSelectionValue, label: `None selected` });
    pages.forEach((page) => {
      options.push({ value: page.id, label: page.title.rendered })
    });
  } else {
    options.push({ value: noSelectionValue, label: 'Loading...', disabled: true, });
  }

  return (
    <>
      <TextControl
        label={`Filter ${postTypePluralName}`}
        value={searchTerm}
        onChange={setSearchTerm}
      />
      <SelectControl
        label={`Select a ${postTypeSingularName}`}
        options={options}
        value={selected}
        onChange={(newId) => {
          if (newId === noSelectionValue) {
            onSelect(undefined);
          } else {
            onSelect(newId);
          }
        }}
      />
    </>
  );
};

const PostSelectionControls = ({
  attributes,
  setAttributes,
  // context,
}) => {
  const { query } = attributes;
  const selected = query.include?.find((element) => element !== undefined);

  return (
    <PanelBody title="Post Selection">
      <PostSelector
        selected={selected}
        postTypeSlug={attributes?.query?.postType}
        onSelect={(newId) => {
          const include = newId == null ? null : [newId];

          setAttributes({
            query: {
              ...query,
              include,
            },
          });
        }}
      />
    </PanelBody>
  );
};

const withExtraControls = (BlockEdit) => (props) => {
  return isVariation(props) ? (
    <>
      <BlockEdit {...props} />
      <InspectorControls>
        <PostSelectionControls {...props} />
      </InspectorControls>
    </>
  ) : (
    <BlockEdit {...props} />
  );
};

addFilter('editor.BlockEdit', QUERY_BLOCK_SLUG, withExtraControls);

registerBlockVariation(
  QUERY_BLOCK_SLUG,
  {
    name: VARIATION_NAME,
    title: 'Single Post Query',
    description: `This block is deprecated! Please use the Selected Post Query block instead.`,
    // description: `Selects a single post or page.`,
    isActive: ['namespace'],
    scope: [],

    attributes: {
      namespace: VARIATION_NAME,
      query: {
        perPage: 1,
        postType: 'post',
        inherit: false,
        include: null,
      },
    },

    // allowedControls: [],

    innerBlocks: [
      [
        'core/post-template',
        {},
        [
          ['core/post-title'],
        ],
      ]
    ]
  }
);
