import { __ } from '@wordpress/i18n';
import {
  InspectorControls,
  InnerBlocks,
  useBlockProps,
  useInnerBlocksProps,
} from '@wordpress/block-editor';
import {
  PanelBody,
  PanelRow,
  ToggleControl,
} from '@wordpress/components';
import { registerBlockType } from '@wordpress/blocks';

import { slugs } from './constants';
import blockDef from './block.json';
import icon from './icon.svg';


const editTemplate = [
  [slugs.title, {}],
  [slugs.body, {}],
];

function Edit({ attributes, setAttributes }) {
  const {
    isDefaultOpen,
  } = attributes;

  return (<>
    <InspectorControls>
      <PanelBody
        initialOpen={true}
      >
        <PanelRow>
          <ToggleControl
            label={isDefaultOpen
              ? __('Initially open')
              : __('Initially closed')
            }
            help={isDefaultOpen
              ? __('Accordion is open when page loads.')
              : __('Accordion is collapsed when page loads.')
            }
            checked={isDefaultOpen}
            onChange={(value) => setAttributes({ isDefaultOpen: value })}
          />
        </PanelRow>
      </PanelBody>
    </InspectorControls>
    <div
      {...useBlockProps()}
    >
      <InnerBlocks template={editTemplate} templateLock="all" />
    </div>
  </>);
}

function Save({ attributes }) {
  const {
    isDefaultOpen,
  } = attributes;

  const blockProps = useInnerBlocksProps.save(
    useBlockProps.save({
      'open': isDefaultOpen,
    })
  );

  return (
    <details
      {...blockProps}
    />
  );
}

registerBlockType(
  blockDef,
  {
    icon: icon,
    edit: Edit,
    save: Save,
  }
);
