import {
  PanelBody,
  SelectControl,
  ToggleControl,
} from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { store as editorStore } from '@wordpress/editor';
import { store as coreStore } from '@wordpress/core-data';


const disallowedTaxonomies = [
  'nav_menu', 
  'wp_pattern_category',
];

const TaxonomyControls = ({
  attributes,
  setAttributes,
}) => {
  let {
    taxonomySlug,
    includeChildren,
    includeParents,
  } = attributes;

  // Determine the type of entity being edited
  const post = useSelect((select) => select(editorStore).getCurrentPost(), []);

  // Get all registered taxonomies
  const taxonomies = useSelect((select) => select(coreStore).getTaxonomies(), []);

  const isTemplate = post?.type === 'wp_template' && post?.template === undefined;
  const fullSlug = post.slug;
  let slug = fullSlug
    .replace(/^tag(\-)?/, 'post_tag$1')
    .replace(/^taxonomy\-/, '');
  let isTaxonomyArchive = false;
  let templateTaxonomy = null;

  // If the current post is a template, then determine if the template is for a taxonomy archive.
  if (isTemplate) {
    templateTaxonomy = taxonomies.find(
      (taxonomy) => {
        // Return true if the current post slug begins with the taxonomy slug.
        return slug.startsWith(taxonomy.slug);
      }
    );

    if (templateTaxonomy) {
      isTaxonomyArchive = true;
      taxonomySlug = templateTaxonomy.slug;
    }
  }

  // Filter the taxonomies to only include those that are not disallowed, 
  // and map them to select options.
  let taxonomyOptions = isTaxonomyArchive
    ? [templateTaxonomy]
    : taxonomies;
  taxonomyOptions = taxonomyOptions
    .filter((taxonomy) => !(disallowedTaxonomies.includes(taxonomy.slug)))
    .map((taxonomy) => ({
      label: taxonomy.name,
      value: taxonomy.slug,
    }));

  // Find the selected taxonomy, and determine if it is hierarchical.
  const selectedTaxonomy = taxonomies.find((taxonomy) => taxonomy.slug === taxonomySlug);
  const isHierarchical = selectedTaxonomy?.hierarchical === true;

  return (
    <PanelBody title="Related Posts Taxonomy">
      <SelectControl
        label="Taxonomy"
        value={taxonomySlug}
        options={taxonomyOptions}
        onChange={(value) => setAttributes({ taxonomySlug: value })}
      />
      {isHierarchical && (
        <>
          <ToggleControl
            label="Include Parent Terms"
            help={
              includeParents
                ? 'Posts belonging to parent terms will be included in the query.'
                : `Parent terms will not be included in the query.`
            }
            checked={includeParents}
            onChange={(value) => setAttributes({ includeParents: value })}
          />
          <ToggleControl
            label="Include Child Terms"
            help={
              includeChildren
                ? 'Posts belonging to child terms will be included in the query.'
                : `Child terms will not be included in the query.`
            }
            checked={includeChildren}
            onChange={(value) => setAttributes({ includeChildren: value })}
          />
        </>
      )}
    </PanelBody>
  );
};

export default TaxonomyControls;
