import { createReduxStore, register } from '@wordpress/data';
import tabLayoutBlockDef from './tab-layout/block.json';


const storeName = tabLayoutBlockDef.name;
const DEFAULT_STATE = {
  tabLayouts: {},
};
const DEFAULT_TAB_LAYOUT_STATE = {
  selectedTabIndex: 0,
};

const actions = {
  setSelectedTabIndex(tabLayoutClientId, selectedTabIndex) {
    return {
      type: 'SET_SELECTED_TAB_INDEX',
      tabLayoutClientId,
      selectedTabIndex,
    };
  },
};

const reducer = (state = DEFAULT_STATE, action) => {
  switch (action.type) {
    case 'SET_SELECTED_TAB_INDEX':
      // console.log(`[editor-data] setting tabLayout ${action.tabLayoutClientId} to tab index ${action.selectedTabIndex}`)
      const newState = {...state};
      newState.tabLayouts[action.tabLayoutClientId] = {
        selectedTabIndex: action.selectedTabIndex,
      };

      return newState;
  }

  return state;
};

const selectors = {
  getSelectedTabIndex(state, tabLayoutClientId) {
    const tabLayout = state.tabLayouts[tabLayoutClientId] || DEFAULT_TAB_LAYOUT_STATE;
    return tabLayout.selectedTabIndex;
  },
};

export const store = createReduxStore(
  storeName,
  {
    reducer,
    actions,
    selectors
  }
);

register(store);
