import { registerBlockVariation } from '@wordpress/blocks';
import { InspectorControls } from '@wordpress/block-editor';
import {
  PanelBody,
  SelectControl,
  ToggleControl,
} from '@wordpress/components';
import { addFilter } from '@wordpress/hooks';
import { __ } from '@wordpress/i18n';
import dayjs from 'dayjs';
import icon from './icon.svg';


const CORE_BLOCK = 'core/paragraph';
const VARIATION_NAME = 'amplify-blocks/current-date';
const attributes = {
  amplifyVariation: VARIATION_NAME,
  metadata: {
    bindings: {
      content: {
        source: "amplify-blocks/data-current-date",
        args: {}
      }
    }
  },
  placeholder: __('Current Date'),
};

addFilter(
  'editor.BlockEdit',
  CORE_BLOCK,
  (BlockEdit) => (props) => {
    const isVariation = props.attributes.amplifyVariation === VARIATION_NAME;

    const now = dayjs();

    return isVariation ? (
      <>
        <BlockEdit {...props} />
        <InspectorControls>
          <PanelBody title="Date Format">
            <SelectControl
              label="Date Format"
              value={props.attributes.dateFormat}
              options={[
                { label: now.format('YYYY'), value: 'Y' },
                { label: now.format('YY'), value: 'y' },
                { label: now.format('MMMM D, YYYY'), value: 'F j, Y' },
                { label: now.format('dddd, MMMM D, YYYY'), value: 'l, F j, Y' },
                { label: now.format('YYYY-MM-DD'), value: 'Y-m-d' },
                { label: now.format('MM/DD/YYYY'), value: 'm/d/Y' },
              ]}
              onChange={(value) => props.setAttributes({ dateFormat: value })}
            />
            <SelectControl
              label="Copyright Prefix"
              value={props.attributes.copyrightPrefix}
              options={[
                { label: `None`, value: '' },
                { label: `Â©`, value: 'copy_symbol' },
                { label: `Copyright`, value: 'copyright_text' },
              ]}
              onChange={(value) => props.setAttributes({ copyrightPrefix: value })}
            />
          </PanelBody>
        </InspectorControls>
      </>
    ) : (
      <BlockEdit {...props} />
    );
  }
);

registerBlockVariation(
  CORE_BLOCK,
  {
    name: VARIATION_NAME,
    title: __('Current Date'),
    keywords: [__('date'), __('time'), __('current')],
    description: __('Display the current date, in a variety of formats.'),
    icon,
    attributes,
    isActive: ['amplifyVariation'],
  }
);

addFilter(
  'blocks.registerBlockType',
  `${CORE_BLOCK}/${VARIATION_NAME}`,
  (settings, name) => {
    if (name !== CORE_BLOCK) {
      return settings;
    }

    return {
      ...settings,
      attributes: {
        ...settings.attributes,
        amplifyVariation: {
          type: 'string',
          default: null,
        },
        dateFormat: {
          type: 'string',
          default: 'Y',
        },
        copyrightPrefix: {
          type: 'string',
          default: '',
        },
      }
    };
  }
);

console.log(`[mbm-gutenblocks] CurrentDate block loaded`)
