import {
  FormTokenField,
  PanelBody,
  ToggleControl,
} from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { useEntityRecords } from '@wordpress/core-data';
import { useState } from '@wordpress/element';


const postTypePluralName = (postType) => postType?.name || 'Posts';

const FormTokenPostSelector = ({
  selected,
  onSelect,
  postType,
}) => {
  const [searchTerm, setSearchTerm] = useState('');

  // Selected post query args
  const selectedQuery = {
    status: 'publish',
    // No limits on the number of posts selected.
    per_page: -1,
    include: selected,
    // If `include` is empty, then orderby:include will cause an error.
    orderby: selected.length === 0 ? 'date' : 'include',
  };

  let { records: selectedPosts } = useEntityRecords('postType', postType.slug, selectedQuery);

  if (!Array.isArray(selectedPosts)) {
    selectedPosts = [];
  }

  // Suggestion post query args
  const suggestionsQuery = {
    status: 'publish',
    per_page: 50,
    search: searchTerm,
    exclude: selected,
  };

  let { records: suggestionsPosts } = useEntityRecords('postType', postType.slug, suggestionsQuery);

  if (!Array.isArray(suggestionsPosts)) {
    suggestionsPosts = [];
  }

  const suggestionsPostsTitles = suggestionsPosts.map((post) => post?.title?.raw)
  const displayTransform = (postIdOrTitle) => {
    let displayValue = postIdOrTitle;

    // If we have a post ID, try to find the post title.
    // Otherwise we're assuming it's a title string.
    if (Number.isInteger(postIdOrTitle)) {
      const post = selectedPosts.find((post) => post.id === postIdOrTitle);
      displayValue = post?.title?.raw;
    }

    return displayValue || 'N/A';
  };
  const onChangeHandler = (tokens) => {
    // WARN: CNTI identified possible bug here.
    // console.log(`[SelectedPostQuery] tokens:`, tokens, `suggestionsPosts:`, suggestionsPosts, `selectedPosts:`, selectedPosts);
    
    const newPosts = new Set();
    for (const newPost of tokens) {
      if (Number.isInteger(newPost)) {
        // Already a post ID, directly add to set
        newPosts.add(newPost);
      } else if (typeof newPost === 'string') {
        const normalizeTitle = (title) => {
          if (typeof title !== 'string') {
            return '';
          }

          return title.trim().toLowerCase();
        }
        const normalizedNewPostTitle = normalizeTitle(newPost);
        const post = suggestionsPosts.find((post) => {
          const postTitleRaw = normalizeTitle(post?.title?.raw);
          const postTitleRendered = normalizeTitle(post?.title?.rendered);

          return postTitleRaw === normalizedNewPostTitle || postTitleRendered === normalizedNewPostTitle;
        });
        if (post?.id) {
          newPosts.add(post.id);
        } else {
          console.error(`[SelectedPostQuery] Could not find suggested post with title: ${newPost}`);
        }
      }
    }

    // console.log(`[SelectedPostQuery] newPosts:`, newPosts);
    onSelect(newPosts);
  };
  const onInputChangeHandler = (searchInput) => {
    setSearchTerm(searchInput);
  };

  return (
    <>
      <FormTokenField
        label={`Select ${postTypePluralName(postType)} To Feature`}
        value={selected}
        suggestions={suggestionsPostsTitles}
        displayTransform={displayTransform}
        onChange={onChangeHandler}
        onInputChange={onInputChangeHandler}
        __experimentalShowHowTo={false}
      />
    </>
  );
};

const PostSelectionControls = ({
  attributes,
  setAttributes,
}) => {
  const { query } = attributes;
  const selected = Array.isArray(query.include) ? query.include : [];
  const randomizeResults = query?.randomizeResults || false;

  const postTypes = useSelect((select) => {
    return select('core').getPostTypes({ per_page: -1 });
  });
  const postTypeSlug = query?.postType;
  const postType = postTypes
    ? postTypes.find((postType) => postType.slug === postTypeSlug)
    : null;

  const hasPostType = postType != null;

  return (
    <PanelBody title="Post Selection">
      {hasPostType && (
        <>
          <FormTokenPostSelector
            selected={selected}
            postType={postType}
            onSelect={(newIds) => {
              const include = [...newIds];

              setAttributes({
                query: {
                  ...query,
                  include,
                  orderby: include.length > 0 ? 'include' : 'date',
                },
              });
            }}
          />
          <ToggleControl
            label="Randomize Results"
            help={
              randomizeResults
                ? 'Guests will see randomized results.'
                : `${postTypePluralName(postType)} will be shown in the order selected.`
            }
            checked={randomizeResults}
            onChange={(randomizeResults) => {
              setAttributes({
                query: {
                  ...query,
                  randomizeResults,
                }
              });
            }}
          />
        </>
      )}
      {!hasPostType && (
        <p>Loading...</p>
      )}
    </PanelBody>
  );
};

export default PostSelectionControls;
