import { registerBlockType } from '@wordpress/blocks';
import blockDef from './block.json';
import Save from './Save';
import Edit from './Edit';
import anyTermsVariationIcon from './any-terms-variation/icon.svg';
import AnyTermsVariation from './any-terms-variation';
import NoTermsVariation from './no-terms-variation';
import SingleTermVariation from './single-term-variation';
import MultipleTermsVariation from './multiple-terms-variation';


registerBlockType(
	blockDef,
	{
		icon: anyTermsVariationIcon,
		edit: Edit,
		save: Save,
    variations: [
			// AnyTermsVariation,
      NoTermsVariation,
			SingleTermVariation,
			MultipleTermsVariation,
    ],
	}
);
